$admin-panel-base-color: DodgerBlue;

.error {
    color: #ff3e57;
}

//reset admin-lte css
.card-header::after,
.card-body::after,
.card-footer::after {
    display: block;
    clear: both;
    content: none;
}


.header {
    font-size: 1rem;
    font-weight: bolder;
}

.normal-line-height {
    line-height: 0.5;
}

.primary-btn-custom {
    background-color: $admin-panel-base-color;
    border: none;
    color: white;
    font-size: 16px;
    cursor: pointer;
}

.label-text {
    font-weight: 200;
    color: #212529;
    line-height: 1.5;
}

.dataTables_length {
    color: transparent;
}
.box-custom {
    display: block;
    width: 100%;
    height: calc(2.25rem + 2px);
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #ffffff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: 0.25rem;
    box-shadow: inset 0 0 0 rgba(0, 0, 0, 0);
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.box-custom:hover {
    box-shadow: inset 0 0 0 rgba(0, 0, 0, 0);
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
    border: 1px solid $admin-panel-base-color;
}
